import { NetworkId } from "@phantom/constants";
var CAIP2_NETWORK_MAPPINGS = {
  // Solana networks
  [NetworkId.SOLANA_MAINNET]: {
    chain: "solana",
    network: "mainnet",
    description: "Solana Mainnet-Beta"
  },
  [NetworkId.SOLANA_DEVNET]: {
    chain: "solana",
    network: "devnet",
    description: "Solana Devnet"
  },
  [NetworkId.SOLANA_TESTNET]: {
    chain: "solana",
    network: "testnet",
    description: "Solana Testnet"
  },
  // Ethereum/EVM networks
  [NetworkId.ETHEREUM_MAINNET]: {
    chain: "ethereum",
    network: "mainnet",
    description: "Ethereum Mainnet"
  },
  [NetworkId.ETHEREUM_SEPOLIA]: {
    chain: "ethereum",
    network: "sepolia",
    description: "Sepolia Testnet"
  },
  [NetworkId.POLYGON_MAINNET]: {
    chain: "polygon",
    network: "mainnet",
    description: "Polygon Mainnet"
  },
  [NetworkId.OPTIMISM_MAINNET]: {
    chain: "optimism",
    network: "mainnet",
    description: "Optimism Mainnet"
  },
  [NetworkId.OPTIMISM_GOERLI]: {
    chain: "optimism",
    network: "goerli",
    description: "Optimism Goerli Testnet"
  },
  [NetworkId.ARBITRUM_ONE]: {
    chain: "arbitrum",
    network: "mainnet",
    description: "Arbitrum One"
  },
  [NetworkId.BASE_MAINNET]: {
    chain: "base",
    network: "mainnet",
    description: "Base Mainnet"
  },
  [NetworkId.BASE_SEPOLIA]: {
    chain: "base",
    network: "sepolia",
    description: "Base Sepolia Testnet"
  },
  // Bitcoin networks (for future support)
  [NetworkId.BITCOIN_MAINNET]: {
    chain: "bitcoin",
    network: "mainnet",
    description: "Bitcoin Mainnet"
  },
  [NetworkId.BITCOIN_TESTNET]: {
    chain: "bitcoin",
    network: "testnet",
    description: "Bitcoin Testnet"
  },
  // Sui networks (for future support)
  [NetworkId.SUI_MAINNET]: {
    chain: "sui",
    network: "mainnet",
    description: "Sui Mainnet"
  },
  [NetworkId.SUI_TESTNET]: {
    chain: "sui",
    network: "testnet",
    description: "Sui Testnet"
  }
};
function deriveSubmissionConfig(networkId) {
  const mapping = CAIP2_NETWORK_MAPPINGS[networkId];
  if (!mapping) {
    return void 0;
  }
  return {
    chain: mapping.chain,
    network: mapping.network
  };
}
function supportsTransactionSubmission(networkId) {
  return networkId in CAIP2_NETWORK_MAPPINGS;
}
function getNetworkDescription(networkId) {
  return CAIP2_NETWORK_MAPPINGS[networkId]?.description;
}
function getSupportedNetworkIds() {
  return Object.keys(CAIP2_NETWORK_MAPPINGS);
}
function getNetworkIdsByChain(chain) {
  return Object.entries(CAIP2_NETWORK_MAPPINGS).filter(([_, mapping]) => mapping.chain.toLowerCase() === chain.toLowerCase()).map(([networkId]) => networkId);
}

